<template>
  <div class="pt-6 pb-6">
    <b-container fluid>
      <p class="h2">  </p>
      <form action="">
        <fieldset>
          <ul class="manage-search">
            <!--  -->
            <li>
              <dl>
                <dt></dt>
                <dd>
                  <div class="d-flex duration">
                    <base-input class="select">
                      <select class="form-control" v-model="durationSelected">
                        <option selected></option>
                      </select>
                    </base-input>

                    <b-form-datepicker
                      id="datepic1"
                      placeholder=""
                      :disabled="disabled"
                      :readonly="readonly"
                      :date-format-options="{ year: 'numeric', month: 'short', day: '2-digit', weekday: 'short' }"
                    ></b-form-datepicker>
                    <span class="wave">~</span>
                    <b-form-datepicker
                      id="datepic2"
                      placeholder=""
                      :disabled="disabled"
                      :readonly="readonly"
                      :date-format-options="{ year: 'numeric', month: 'numeric', day: '2-digit' }"
                      locale="en"
                    ></b-form-datepicker>

                    <base-button size="sm" type="secondary"></base-button>
                    <base-button size="sm" type="secondary">1</base-button>
                    <base-button size="sm" type="secondary">1</base-button>
                    <base-button size="sm" type="secondary">3</base-button>
                    <base-button size="sm" type="secondary">6</base-button>
                    <base-button size="sm" type="secondary">1</base-button>
                  </div>
                </dd>
              </dl>
            </li>
            <!--  -->
            <li>
              <dl>
                <dt></dt>
                <dd>
                  <div class="d-flex">
                    <div class="custom-control custom-checkbox">
                      <input id="authCheck1" @click="allAuthChecked" v-model="isAuthAllCheck" type="checkbox" name="allAuthCheck" autocomplete="off" class="custom-control-input" value="all">
                      <label for="authCheck1" class="custom-control-label"></label>
                    </div>

                    <div class="custom-control custom-checkbox">
                      <input id="authCheck2" @change="authChecked" v-model="authCheckList" type="checkbox" name="authCheck" autocomplete="off" class="custom-control-input" value="1">
                      <label for="authCheck2" class="custom-control-label"></label>
                    </div>

                    <div class="custom-control custom-checkbox">
                      <input id="authCheck3" @change="authChecked" v-model="authCheckList" type="checkbox" name="authCheck" autocomplete="off" class="custom-control-input" value="2">
                      <label for="authCheck3" class="custom-control-label"></label>
                    </div>
                  </div>
                </dd>
              </dl>
            </li>
            <!--   -->
            <li>
              <dl>
                <dt></dt>
                <dd>
                  <div class="d-flex">
                    <div class="custom-control custom-checkbox">
                      <input id="usedCheck1" @click="allUsedChecked" v-model="isUsedAllCheck" type="checkbox" name="allUsedCheck" autocomplete="off" class="custom-control-input" value="all">
                      <label for="usedCheck1" class="custom-control-label"></label>
                    </div>

                    <div class="custom-control custom-checkbox">
                      <input id="usedCheck2" @change="usedChecked" v-model="usedCheckList" type="checkbox" name="usedCheck" autocomplete="off" class="custom-control-input" value="1">
                      <label for="usedCheck2" class="custom-control-label"></label>
                    </div>

                    <div class="custom-control custom-checkbox">
                      <input id="usedCheck3" @change="usedChecked" v-model="usedCheckList" type="checkbox" name="usedCheck" autocomplete="off" class="custom-control-input" value="2">
                      <label for="usedCheck3" class="custom-control-label"></label>
                    </div>
                  </div>
                </dd>
              </dl>
            </li>
            <!--  -->
            <li>
              <dl>
                <dt></dt>
                <dd>
                  <div class="d-flex keywords">
                    <base-input class="select">
                      <select class="form-control" v-model="keywordsSelected">
                        <option selected></option>
                        <option></option>
                        <option></option>
                        <option></option>
                      </select>
                    </base-input>

                    <div class="form-input">
                      <input type="text" placeholder=" " class="form-control" valid="true" v-model="inputKeword">
                    </div>

                    <base-button size="sm" type="primary"></base-button>
                    <base-button size="sm" type="secondary" @click="resetForm"></base-button>
                  </div>
                </dd>
              </dl>
            </li>
          </ul>
        </fieldset>
      </form>
    </b-container>
    
    <b-container fluid class="mt-4">
      <div class="manage-list">
        <p class="h2">  </p>
        <div class="btn-wrap">
          <base-button size="sm" type="danger"></base-button>
          <base-button size="sm" type="warning" @click="gotoWrite"></base-button>
        </div>
        <div class="table-form">
          <table>
            <colgroup>
              <col width="3%">
              <col width="5%">
              <col width="15%">
              <col width="15%">
              <col width="15%">
              <col width="15%">
              <col width="15%">
              <col width="">
            </colgroup>
            <thead>
              <th>
                <div class="custom-control custom-checkbox">
                  <input id="managerAll" @click="allManagerChecked" v-model="isManagerAllCheck" type="checkbox" name="managerAll" autocomplete="off" class="custom-control-input" value="dd">
                  <label for="managerAll" class="custom-control-label"></label>
                </div>
              </th>
              <th><span class="text-sm"></span></th>
              <th><span class="text-sm"></span></th>
              <th><span class="text-sm"></span></th>
              <th><span class="text-sm"></span></th>
              <th><span class="text-sm"></span></th>
              <th><span class="text-sm"></span></th>
              <th><span class="text-sm"></span></th>
            </thead>
            <tbody>
              <tr v-for="user in managerRealList" :key="user.id">
                <td>
                  <div class="custom-control custom-checkbox">
                    <input :id="`manager${user.id}`" @change="managerChecked" v-model="managerCheckList" type="checkbox" name="manager" autocomplete="off" class="custom-control-input" :value="`${user.id}`">
                    <label :for="`manager${user.id}`" class="custom-control-label"></label>
                  </div>
                </td>
                <td><span class="text-sm">{{ user.id }}</span></td>
                <td><span class="text-sm">{{ user.userId }}</span></td>
                <td><span class="text-sm">{{ user.title }}</span></td>
                <td><span class="text-sm"><!-- {{ user.website }} --></span></td>
                <td><span class="text-sm"><!-- {{ user.phone }} --></span></td>
                <td><span class="text-sm"><!-- {{ user.company.name }} --></span></td>
                <td><span class="text-sm"><!-- {{ user.address.zipcode }} --></span></td>
              </tr>
            </tbody>
          </table>
          <b-pagination
            v-model="currentPage"
            :total-rows="this.manager.length / 10"
            :per-page="1"
            :align="`right`"
            first-number
            last-number
            @change="changePaging"
          ></b-pagination>
        </div>
      </div>
    </b-container>
  </div>
</template>

<script>
import { fetchMangerList } from "../api/index";
export default {
  data() {
    return {
      //
      durationSelected: "",
      //  
      authData: ["1", "2"],
      authCheckList: [],
      isAuthAllCheck: false,
      //  
      usedData: ["1", "2"],
      usedCheckList: [],
      isUsedAllCheck: false,
      // 
      keywordsSelected: "",
      inputKeword: "",
      //datapicker
      value: "",
      disabled: false,
      readonly: false,
      selected: "1",
      model: [],
      // 
      manager: [],
      managerRealList: [],
      managerData: [],
      isManagerAllCheck: false,
      managerCheckList: [],
      currentPage: 1,
      totalPage: 0,
    }
  },
  methods: {
    allAuthChecked() {
      this.isAuthAllCheck = !this.isAuthAllCheck;
      this.authCheckList = [];
      if(this.isAuthAllCheck) {
        for(let key in this.authData) {
          this.authCheckList.push(this.authData[key]);
        }
      }
    },
    authChecked() {
      this.authData.length == this.authCheckList.length ? this.isAuthAllCheck = true : this.isAuthAllCheck = false;
    },
    allUsedChecked() {
      this.isUsedAllCheck = !this.isUsedAllCheck;
      this.usedCheckList = [];
      if(this.isUsedAllCheck) {
        for(let key in this.usedData) {
          this.usedCheckList.push(this.usedData[key]);
        }
      }
    },
    usedChecked() {
      this.usedData.length == this.usedCheckList.length ? this.isUsedAllCheck = true : this.isUsedAllCheck = false;
    },
    allManagerChecked() {
      this.isManagerAllCheck = !this.isManagerAllCheck;
      this.managerCheckList = [];
      if(this.isManagerAllCheck) {
        for(let key in this.managerData) {
          this.managerCheckList.push(this.managerData[key]);
        }
      }
    },
    managerChecked() {
      this.managerData.length == this.managerCheckList.length ? this.isManagerAllCheck = true : this.isManagerAllCheck = false;
    },
    changePaging(val) {
      fetchMangerList()
        .then(response => {
          this.managerRealList = response.data.slice((val - 1) * 10, (val - 1) * 10 + 10);
          this.managerData = [];
          this.managerRealList.reverse();
          this.managerRealList.forEach((val, i) => {
            this.managerData.push(val.id);
          })
        })
        .catch(error => console.log(error));
    },
    resetForm() {
      this.isAuthAllCheck = false;
      this.authCheckList = [];
      this.isUsedAllCheck = false;
      this.usedCheckList = [];
      this.keywordsSelected = "";
      this.inputKeword = "";
    },
    gotoWrite() {
      this.$router.push("icons")
    }
  },
  created () {
    fetchMangerList()
      .then(response => {
        this.manager = response.data;
        this.managerData = [];
        this.managerRealList = response.data.slice(0, 10);
        this.managerRealList.reverse();
        this.managerRealList.forEach((val, i) => {
          this.managerData.push(val.id);
        })
      })
      .catch(error => console.log(error));
  },
}
</script>

